import React, { useState } from "react";
import { ThemeProvider } from "styled-components";
import { lightTheme, darkTheme } from "./themes";
import { GlobalStyles } from "./global";
import { useTranslation } from "react-i18next";

import "./i18n";
import { GB, RU } from "country-flag-icons/react/3x2";

import { Button, Switch, FormControlLabel } from "@mui/material";
import { styled } from "@mui/material/styles";
import { Brightness4, Brightness7 } from "@mui/icons-material";

function App() {
    const { t, i18n } = useTranslation();
    const [theme, setTheme] = useState("light");

    const toggleTheme = () => {
        setTheme(theme === "light" ? "dark" : "light");
    };

    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
    };
    const BlackSwitch = styled(Switch)(({ theme }) => ({
        "& .MuiSwitch-switchBase": {
            color: "#ffffff", // Black color for the switch handle
            "&.Mui-checked": {
                color: "#ffffff", // Black color when checked
            },
            "&.Mui-checked + .MuiSwitch-track": {
                backgroundColor: "#e0e0e0", // Black color for the track when checked
            },
        },
        "& .MuiSwitch-track": {
            backgroundColor: "#e0e0e0", // Light grey color for the track when not checked
        },
    }));
    return (
        <ThemeProvider theme={theme === "light" ? lightTheme : darkTheme}>
            <GlobalStyles />
            <header
                style={{
                    display: "flex",
                    justifyContent: "flex-end",
                    padding: "1rem",
                    alignItems: "center",
                    backgroundColor: "#020421", // Dark blue color
                    color: "#ffffff", // Optional: Sets text color to white for better contrast
                }}
            >
                <Button
                    onClick={() => changeLanguage("ru")}
                    style={{ marginRight: "8px" }}
                >
                    <RU style={{ width: "24px", height: "16px" }} />
                </Button>
                <Button
                    onClick={() => changeLanguage("en")}
                    style={{ marginRight: "8px" }}
                >
                    <GB style={{ width: "24px", height: "16px" }} />
                </Button>
                <FormControlLabel
                    control={
                        <BlackSwitch
                            checked={theme === "dark"}
                            onChange={toggleTheme}
                        />
                    }
                    label={
                        theme === "dark" ? (
                            <Brightness4 />
                        ) : (
                            <Brightness7 />
                        )
                    }
                />
            </header>
            <main
                style={{
                    display: "flex",
                    flexDirection: "column", // Stacks the items vertically
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100vh",
                    textAlign: "center", // Centers the text within each heading
                }}
            >
                <h1>{t("welcome")}</h1>
                <h2>{t("maintenance")}</h2>
            </main>
        </ThemeProvider>
    );
}

export default App;
